:0}.rename-hover{cursor:text;display:inline-block}.rename-hover:hover{text-decoration:underline;text-decoration-color:var(--color-placeholder-light);text-underline-offset:5px;text-decoration-thickness:1px}.memories-livephoto{position:relative;overflow:hidden}.memories-livephoto img,.memories-livephoto video{position:absolute;padding:inherit;top:0;left:0;width:100%;height:100%;display:block;z-index:1;transition:transform .3s ease-in-out,visibility .3s ease-in-out,opacity .3s ease-in-out}.memories-livephoto video{opacity:0;z-index:2;visibility:hidden;pointer-events:none}.memories-livephoto.playing.canplay video{opacity:1;visibility:visible;transition:opacity .3s ease-in-out,visibility 0s ease-in-out}.memories-livephoto.playing.canplay img{transform:scale(1.05)}.v-popper--theme-dropdown .v-popper__inner{max-height:calc(90vh - 86px) !important}[role=note].empty-content{width:100%;height:100%;text-align:center;padding:10px}li.list-item__wrapper>a.list-item{box-sizing:border-box}div.oc-dialog[role=dialog]{min-width:min(400px,90vw)}div.oc-dialog[role=dialog].dialog-prompt .ui-icon,div.oc-dialog[role=dialog].dialog-prompt label{display:none}div.oc-dialog[role=dialog].dialog-prompt input[type=text]{width:100%}html.native #content-vue{margin-top:0 !important;height:100vh !important}html.native header#header{display:none !important}html.native,html.native body{background:rgba(0,0,0,0) !important}html.native body.viewer-fully-opened.viewer-video #app-content-vue{visibility:hidden;opacity:0}html.native body.viewer-fully-opened.viewer-video .pswp__bg{display:none}html.native body.viewer-fully-opened.viewer-video .pswp__item.active{display:none !important}`, "",{"version":3,"sources":["webpack://./styles/body.scss","webpack://./styles/header.scss","webpack://./styles/utils.scss","webpack://./styles/livephoto.scss","webpack://./styles/patch.scss","webpack://./styles/native.scss"],"names":[],"mappings":"AAAA,MAGE,+DAAA,CAGF,KACE,eAAA,CAEA,OACE,yCAAA,CACA,0BAAA,CAKJ,aAGE,eAAA,CACA,iDAAA,CACA,cAAA,CAGA,2BACE,UAAA,CACA,aAAA,CAVJ,aAcE,gBAAA,CAGA,gBAAA,CAEA,gCAEE,4CAAA,CACA,yCAAA,CAGA,mDAAA,CACA,sDAAA,CAEA,uDAEE,OAAA,CACA,kCAAA,CACA,8BAAA,CACA,UAAA,CACA,6DACE,SAAA,CAQR,iBACE,iBAAA,CC1DA,0BADF,mCAEI,oBAAA,CAAA,CAGJ,kDACE,oBAAA,CAIF,8BACE,kCAAA,CACA,qCACE,oBAAA,CAKJ,oBACE,yCAAA,CAEA,kCACE,8EAAA,CAJJ,oBAOE,WAAA,CACA,0BACE,SAAA,CC3BJ,YACE,UAAA,CACA,WAAA,CACA,aAAA,CAIF,UACE,iBAAA,CACA,KAAA,CACA,MAAA,CAYF,gBAPE,oBAAA,CACA,uBAAA,CACA,mCACE,YAAA,CACA,kBAAA,CAOF,yBADF,uBAVE,oBAAA,CACA,uBAAA,CACA,0CACE,YAAA,CACA,kBAAA,CAAA,CAaJ,gBACE,gBAAA,CACA,wBAAA,CAIF,mBACE,oBAAA,CAGA,4FAEE,uBAAA,CACA,QAAA,CAKJ,cACE,WAAA,CACA,oBAAA,CAEA,oBACE,yBAAA,CACA,oDAAA,CACA,yBAAA,CACA,6BAAA,CC1DJ,oBACE,iBAAA,CACA,eAAA,CAEA,kDAEE,iBAAA,CACA,eAAA,CACA,KAAA,CACA,MAAA,CACA,UAAA,CACA,WAAA,CACA,aAAA,CACA,SAAA,CACA,uFACE,CAKJ,0BACE,SAAA,CACA,SAAA,CACA,iBAAA,CACA,mBAAA,CAIA,0CACE,SAAA,CACA,kBAAA,CACA,4DACE,CAGJ,wCACE,qBAAA,CCpCN,2CACE,uCAAA,CAIF,0BACE,UAAA,CACA,WAAA,CACA,iBAAA,CACA,YAAA,CAKA,kCACE,qBAAA,CAIJ,2BAEE,yBAAA,CAIE,iGAEE,YAAA,CAGF,0DACE,UAAA,CC7BJ,yBACE,uBAAA,CACA,uBAAA,CAIF,0BACE,uBAAA,CAIF,6BAEE,mCAAA,CAQA,mEAGE,iBAAA,CACA,SAAA,CAIF,4DACE,YAAA,CAIF,qEACE,uBAAA","sourcesContent":[":root {\n  // https://github.com/nextcloud/server/blob/0cd536786b9df555e2e6110f98b41856f0d51313/core/css/apps.scss#L29\n  // This is only used for #content and not actually for the body element\n  --body-height: calc(100% - 50px - var(--body-container-margin));\n}\n\nbody {\n  overflow: hidden;\n\n  * {\n    -webkit-tap-highlight-color: transparent;\n    -webkit-touch-callout: none;\n  }\n}\n\n// Nextcloud 25+: get rid of gap and border radius at right\n#content-vue {\n  // was var(--body-container-radius)\n  // now set on .app-navigation\n  border-radius: 0;\n  width: calc(100% - var(--body-container-margin) * 1); // was *2\n  margin-right: 0;\n\n  // If no navigation remove both margins\n  &:not(.has-nav) {\n    width: calc(100%);\n    margin-left: 0;\n  }\n\n  // Prevent content overflow on NC <25\n  max-height: 100vh;\n\n  // https://bugs.webkit.org/show_bug.cgi?id=160953\n  overflow: visible;\n\n  > div.app-navigation {\n    // Reduce size of navigation bar\n    --app-navigation-max-width: 250px !important;\n    max-width: var(--app-navigation-max-width); // NC <29\n\n    // Rounded corners only on left\n    border-top-left-radius: var(--body-container-radius);\n    border-bottom-left-radius: var(--body-container-radius);\n\n    .app-navigation-toggle {\n      // Move up to align with back button of top matter\n      top: 2px;\n      transition: opacity 0.1s ease-in-out;\n      background-color: transparent;\n      opacity: 0.2; // It's ugly\n      &:hover {\n        opacity: 1;\n      }\n    }\n  }\n}\n\n// Hide horizontal scrollbar on mobile\n// For the padding removal above\n#app-content-vue {\n  overflow-x: hidden;\n}\n","// Top bar is above everything else on mobile\nbody:has(.memories-top-bar) > header {\n  @media (max-width: 1024px) {\n    z-index: 0 !important;\n  }\n}\nbody:has(.memories_viewer, .search-overlay) > header {\n  z-index: 0 !important;\n}\n\n// Allow hiding app name (PublicShareHeader)\nheader#header .header-appname {\n  transition: opacity 0.2s ease-in-out;\n  &.hidden {\n    opacity: 0 !important;\n  }\n}\n\n// Our menu buttons\n.memories-menu-item {\n  background-color: transparent !important;\n\n  header#header & {\n    color: var(--color-background-plain-text, var(--color-primary-text)) !important;\n  }\n\n  opacity: 0.85;\n  &:hover {\n    opacity: 1;\n  }\n}\n","// Fill all available space\n.fill-block {\n  width: 100%;\n  height: 100%;\n  display: block;\n}\n\n// Go to top left corner with absolute positioning\n.top-left {\n  position: absolute;\n  top: 0;\n  left: 0;\n}\n\n// Hide scrollbar\n@mixin hide-scrollbar {\n  scrollbar-width: none;\n  -ms-overflow-style: none;\n  &::-webkit-scrollbar {\n    display: none;\n    width: 0 !important;\n  }\n}\n.hide-scrollbar {\n  @include hide-scrollbar;\n}\n.hide-scrollbar-mobile {\n  @media (max-width: 768px) {\n    @include hide-scrollbar;\n  }\n}\n\n// Disable user selection\n.no-user-select {\n  user-select: none;\n  -webkit-user-select: none; // iOS Safari is a horrible browser\n}\n\n// Hide arrows on number input\ninput[type='number'] {\n  appearance: textfield;\n\n  // https://www.w3schools.com/howto/howto_css_hide_arrow_number.asp\n  &::-webkit-outer-spin-button,\n  &::-webkit-inner-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n  }\n}\n\n// Show the hover cursor and an underline\n.rename-hover {\n  cursor: text;\n  display: inline-block;\n\n  &:hover {\n    text-decoration: underline;\n    text-decoration-color: var(--color-placeholder-light);\n    text-underline-offset: 5px;\n    text-decoration-thickness: 1px;\n  }\n}\n","// Live Photo transitions\n.memories-livephoto {\n  position: relative;\n  overflow: hidden;\n\n  img,\n  video {\n    position: absolute;\n    padding: inherit;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    display: block;\n    z-index: 1;\n    transition:\n      transform 0.3s ease-in-out,\n      visibility 0.3s ease-in-out,\n      opacity 0.3s ease-in-out;\n  }\n\n  video {\n    opacity: 0;\n    z-index: 2;\n    visibility: hidden;\n    pointer-events: none;\n  }\n\n  &.playing.canplay {\n    video {\n      opacity: 1;\n      visibility: visible;\n      transition:\n        opacity 0.3s ease-in-out,\n        visibility 0s ease-in-out;\n    }\n    img {\n      transform: scale(1.05);\n    }\n  }\n}\n","// Make popper menus full height\n.v-popper--theme-dropdown .v-popper__inner {\n  max-height: calc(90vh - 86px) !important; // was 50vh\n}\n\n// Make sure empty content is full width\n[role='note'].empty-content {\n  width: 100%;\n  height: 100%;\n  text-align: center;\n  padding: 10px;\n}\n\n// Make NcListItem border-box to prevent overflow\nli.list-item__wrapper {\n  > a.list-item {\n    box-sizing: border-box;\n  }\n}\n\ndiv.oc-dialog[role='dialog'] {\n  // Make OC dialogs larger to a minimum width\n  min-width: min(400px, 90vw);\n\n  // Hide icon and label in prompts\n  &.dialog-prompt {\n    .ui-icon,\n    label {\n      display: none;\n    }\n\n    input[type='text'] {\n      width: 100%;\n    }\n  }\n}\n","// Native app styles\nhtml.native {\n  // Fill up space\n  #content-vue {\n    margin-top: 0 !important;\n    height: 100vh !important;\n  }\n\n  // Native app has no header\n  header#header {\n    display: none !important;\n  }\n\n  // Pass through to the element below\n  &,\n  body {\n    background: transparent !important;\n  }\n\n  body.viewer-fully-opened.viewer-video {\n    // Hide main content when playing a video,\n    // so we can pass through to the native video player.\n    // The photoswipe element is outside the main content,\n    // so the controls will still be visible.\n    #app-content-vue {\n      // scroller cursors have transitions on visibility\n      // so we also need to set opacity to 0\n      visibility: hidden;\n      opacity: 0;\n    }\n\n    // Hide the background to be transparent for ExoPlayer\n    .pswp__bg {\n      display: none;\n    }\n\n    // Hide the active slide\n    .pswp__item.active {\n      display: none !important;\n    }\n  }\n}\n"],"sourceRoot":""}]);
